export default {
	data() {
		return {

		};
	},
	methods: {
		appOAuthLogin(e) {
			let me = this;
			// 获取用户的登录类型
			let logintype = e.currentTarget.dataset.logintype;

			// 授权登录
			uni.login({
				provider: logintype,
				success(loginRes) {

					// 授权登录成功以后，获取用户的信息
					uni.getUserInfo({
						provider: logintype,
						success(info) {
							// console.log(JSON.stringify(info));

							let userInfo = info.userInfo;
							let face = "";
							let nickname = "";
							let openIdOrUid = "";
							if (logintype == "weixin") {
								face = userInfo.avatarUrl;
								nickname = userInfo.nickName;
								openIdOrUid = userInfo.openId;
							} else if (logintype == "qq") {
								openIdOrUid = userInfo.openId;
								nickname = userInfo.nickname;
								face = userInfo.figureurl_qq_2;
							} else if (logintype == "sinaweibo") {
								openIdOrUid = userInfo.id;
								nickname = userInfo.nickname;
								face = userInfo.avatar_large;
							}

							// 调用开发者后台，执行一键注册或登录
							uni.request({
								url: me.serverURL + "/appUnionLogin/" + logintype,
								data: {
									"openIdOrUid": openIdOrUid,
									"nickname": nickname,
									"face": face
								},
								method: "POST",
								success(result) {
									if (result.data.status == 200) {
										let userInfo = result.data.data;
										// 保存用户信息到全局的缓存中
										uni.setStorageSync("globalUser", userInfo);
										// 切换页面跳转，使用tab切换的api
										uni.switchTab({
											url: "../profile/profile"
										});
									}
								}
							})

						}
					})

				}
			});
		},
		// 实现在微信小程序端的微信登录
		wxLogin(e) {
			let me = this;
			// console.log(e);
			// 通过微信开发能力，获得微信用户的基本信息
			let userInfo = e.detail.userInfo;

			// 实现微信登录
			uni.login({
				provider: "weixin",
				success(loginResult) {
					// console.log(loginResult);
					// 获得微信登录的code：授权码
					let code = loginResult.code;
					// 设置登录到哪个对于的微信小程序，大家可以根据自己的后端去实现业务参数
					// [0:NEXT超英预告][1:超英预告][2:NEXT学院电影预告]
					let loginToWhichMP = 1;
					uni.request({
						url: me.serverURL + "/mpWXLogin/" + code,
						data: {
							"avatarUrl": userInfo.avatarUrl,
							"nickName": userInfo.nickName,
							"whichMP": loginToWhichMP
						},
						method: "POST",
						success(userResult) {
							console.log(userResult);
							let userInfo = userResult.data.data;
							// 保存用户信息到全局的缓存中
							uni.setStorageSync("globalUser", userInfo);
							// 切换页面跳转，使用tab切换的api
							uni.switchTab({
								url: "../profile/profile"
							});
						}
					});

				}
			})
		},
		formSubmit(e) {
			let me = this;
			let username = e.detail.value.username;
			let password = e.detail.value.password;

			// 发起注册/登录的请求
			let serverURL = me.serverURL;
			uni.request({
				url: serverURL + '/user/registOrLogin',
				data: {
					qq: this.qq,
					"username": username,
					"password": password
				},
				header: this.header,
				method: "POST",
				success: (res) => {

					// 获取真实数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						let userInfo = res.data.data;
						// console.log(userInfo);
						// 保存用户信息到全局的缓存中
						uni.setStorageSync("globalUser", userInfo);
						// 切换页面跳转，使用tab切换的api
						uni.switchTab({
							url: "../profile/profile"
						});
					} else if (res.data.status == 500) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							image: "../../static/icos/error.png"
						})
					}
				}
			});
		}
	}
}
